import SysSmartMsg from "./SysSmartMsg";
import {useEffect, useState} from "react";
import {handleUndefined} from "../static/js/stringUtil";

const SysHead = ({left, leftClick, right, rightClick, mid}) => {
    const [headerActive, setHeaderActive] = useState(false);
    leftClick = handleUndefined(leftClick, undefined);
    rightClick = handleUndefined(rightClick, undefined);
    useEffect(() => {
        document.getElementsByClassName("sys-main")[0].addEventListener("scroll", function (e) {
            const upValue = e.target.scrollTop;
            if (upValue > 50) {
                setHeaderActive(true);
            } else {
                setHeaderActive(false);
            }
        });
    });
    return (
        <div>
            <SysSmartMsg></SysSmartMsg>
            <div className={headerActive ? "sys-head active" : "sys-head"}>
                <div className="sys-head-left" onClick={leftClick}>{left}</div>
                <div className="sys-head-mid">{mid}</div>
                <div className="sys-head-right" onClick={rightClick}>{right}</div>
            </div>
        </div>
    );
}

export default SysHead;