import {CheckCircleOutline, CloseCircleOutline} from "antd-mobile-icons";
import {isNotUndefined} from "../static/js/stringUtil";

const SysPopup = ({title, show, setShow, saveData, type, children}) => {
    let topValue;
    if (type) {
        switch (type) {
            case 'small':
                topValue = '80vh';
                break;
            case 'mid':
                topValue = '50vh';
                break;
            case 'big':
                topValue = '0';
                break;
            default :
                topValue = '0';
        }
    }
    console.log(topValue);
    return (
        <div className={show ? "sys-popup show" : "sys-popup"}>
            <div className="head">
                <div className="left"></div>
                <div className="mid">{title}</div>
                <div className="right"></div>
            </div>
            <div className="main">
                {children}
            </div>
            <div className="foot">
                <div className="btn" onClick={() => {
                    setShow(false);
                }}>
                    <CloseCircleOutline></CloseCircleOutline>
                </div>
                <div style={{display: isNotUndefined(saveData) ? 'undefine' : 'none'}} className="btn"
                     onClick={() => saveData()}>
                    <CheckCircleOutline></CheckCircleOutline>
                </div>
            </div>
        </div>
    );
}
export default SysPopup;