import "../../../static/css/data/dataSource.css"
import {useState} from "react";
import {AddOutline, CheckCircleOutline, CloseCircleOutline} from "antd-mobile-icons";
import SysInput from "../../../component/SysInput";
import dataUtil from "../../../static/js/dataUtil";

function DataSource() {
    const dataTypeInput = {
        title: "",
        code: "dataType",
        type: "select",
        options: [
            {name: "", code: "time"},
            {name: "", code: "plan"},
            {name: "", code: "record"},
            {name: "", code: "document"},
        ],
    };
    const dataAppTypeListMap = {
        time: [
            {name: "", code: "countDown"},
            {name: "", code: "event"},
        ],
        plan: [
            {name: "", code: "todo"},
            {name: "", code: "timeLine"},
        ],
        record: [],
        document: [],
    };
    const dataAppInputListMap = {
        countDown: [
            {title: "", code: "cdTitle", type: "text"},
            {title: "", code: "cdHM", type: "time"},
        ],
        event: [
            {title: "", code: "teTime", type: "date"},
            {title: "", code: "teDate", type: "time"},
            {title: "", code: "teTitle", type: "text"},
            {title: "", code: "teInfo", type: "text"},
        ],
        todo: [
            {title: "", code: "todoTitle", type: "text"},
            {title: "", code: "todoTime", type: "time"},
            {title: "", code: "todoTime", type: "checkbox"},
        ],
        timeLine: [
            {title: "", code: "todoTitle", type: "text"},
            {title: "", code: "teTime", type: "date"},
            {title: "", code: "todoTime", type: "time"},
        ],
    };
    const [open, setOpen] = useState(false);
    const [dataType, setDataType] = useState("");
    const [dataAppTypeList, setDataAppTypeList] = useState(dataAppTypeListMap.time);
    const dataAppTypeInput = {title: "", code: "dataAppType", type: "select", options: dataAppTypeList};
    const [dataAppType, setDataAppType] = useState("");
    const [dataAppInputList, setDataAppInputList] = useState(dataAppInputListMap.countDown);
    const [appInputValue, setAppInputValue] = useState({})

    function initForm() {
        setDataType(dataTypeInput.options[0].code);
        setDataAppTypeList(dataAppTypeListMap.time);
        setDataAppType(dataAppTypeListMap.time[0].code);
        setDataAppInputList(dataAppInputListMap.countDown);
    }

    function changeDataAppTypeList(dataType) {
        let dataAppTypeList = dataAppTypeListMap[dataType];
        if (!dataAppTypeList) {
            dataAppTypeList = [];
        }
        setDataAppTypeList(dataAppTypeList);
        if (dataAppTypeList.length !== 0) {
            setDataAppType(dataAppTypeList[0].code);
            changeDataAppInputList(dataAppTypeList[0].code);
        } else {
            setDataAppType("");
            setDataAppInputList([]);
        }
    }

    function changeDataAppInputList(dataAppType) {
        let dataAppInputList = dataAppInputListMap[dataAppType];
        if (!dataAppInputList) {
            dataAppInputList = [];
        }
        setDataAppInputList(dataAppInputList);
    }

    function saveData() {
        setOpen(false);
        const dataKey = dataType + "_" + dataAppType;
        dataUtil.pushDataList(dataKey, appInputValue);
    }

    return (
        <div className="data-source">
            <div className={open ? "data-source-main open" : "data-source-main"}>
                <div className="head">
                    <div className="left"></div>
                    <div className="mid"></div>
                    <div className="right"></div>
                </div>
                <div className="main">
                    <SysInput title={dataTypeInput.title} type={dataTypeInput.type} value={dataType}
                              setValue={(value) => {
                                  setDataType(value);
                                  changeDataAppTypeList(value);
                              }}
                              options={dataTypeInput.options}/>
                    <SysInput title={dataAppTypeInput.title} type={dataAppTypeInput.type} value={dataAppType}
                              setValue={(value) => {
                                  setDataAppType(value);
                                  changeDataAppInputList(value);
                              }}
                              options={dataAppTypeInput.options}/>
                    {
                        dataAppInputList.map((dataAppInput, index) => {
                            return (
                                <div key={index}>
                                    <SysInput title={dataAppInput.title}
                                              type={dataAppInput.type}
                                              setValue={(value) => {
                                                  const newDataAppInput = {
                                                      ...appInputValue
                                                  };
                                                  newDataAppInput[dataAppInput.code] = value;
                                                  setAppInputValue(newDataAppInput);
                                              }}
                                              options={dataAppInput.options}/>
                                </div>
                            );
                        })
                    }
                </div>
                <div className="foot">
                    <div className="btn" onClick={() => {
                        setOpen(false);
                    }}>
                        <CloseCircleOutline></CloseCircleOutline>
                    </div>
                    <div className="btn" onClick={() => saveData()}>
                        <CheckCircleOutline></CheckCircleOutline>
                    </div>
                </div>
            </div>
            <div className="data-source-bottom">
                <div className={open ? "flex-center data-source-btn open" : "flex-center data-source-btn"}
                     onClick={() => {
                         initForm();
                         setOpen(true);
                     }}>
                    <AddOutline/>
                </div>
            </div>
        </div>
    );
}

export default DataSource;