const SysMain = ({type, children}) => {
    let classTypeName = "all";
    if (type) {
        switch (type) {
            case 'topMain':
                classTypeName = "top";
                break;
            case 'bottomMain':
                classTypeName = "bottom";
                break;
            default:
                classTypeName = "all";
                break;
        }
    }
    return (
        <div className={"sys-main " + classTypeName}>
            {children}
        </div>
    );
};
export default SysMain;