import {handleUndefined} from "../static/js/stringUtil";
import {useState} from "react";

const SysInput = ({title, type, value, setValue, options}) => {
    const [inputValue, setInputValue] = useState();
    options = handleUndefined(options, []);
    setValue = handleUndefined(setValue, () => {
    });
    return (
        <div className="input">
            <div className="input-title">{title}</div>
            <div className="input-content flex-center">
                {
                    type === "select" ? (
                        <select value={value ? value : inputValue} onChange={(e) => {
                            setInputValue(e.target.value);
                            setValue(e.target.value);
                        }}>
                            {
                                options.map((item, index) => {
                                    return (<option key={index} value={item.code}>{item.name}</option>);
                                })
                            }
                        </select>
                    ) : (
                        <input type={type} value={value ? value : inputValue} onInput={(e) => {
                            let inputValue = e.target.value;
                            setInputValue(inputValue);
                            setValue(inputValue);
                            if (type === "checkbox") {
                                setValue(e.target.checked);
                            }
                        }}/>
                    )
                }
            </div>
        </div>
    );
}

export default SysInput;