import {Toast} from "antd-mobile";
import {uuid} from "./stringUtil";

const dataTypes = ['time_countDown', 'time_event', 'plan_todo', 'plan_timeLine'];
const dataTypeMap = {
    time_countDown: '-',
    time_event: '-',
    plan_todo: '-',
    plan_timeLine: '-',
};
const dataUtil = {
    pushDataList(key, newData, update) {
        let dataList = this.getDataList(key);
        if (!dataList) {
            dataList = [];
        }
        if (!update) {
            newData.id = uuid();
            newData.createTime = new Date().getTime();
            dataList.push(newData);
            localStorage.setItem(key, JSON.stringify(dataList));
            Toast.show({
                icon: 'success',
                content: '',
            });
        }
        if (update) {
            dataList.push(newData);
            localStorage.setItem(key, JSON.stringify(dataList));
            Toast.show({
                icon: 'success',
                content: '',
            });
        }
    },
    getDataList(key) {
        const dataListJson = localStorage.getItem(key);
        return JSON.parse(dataListJson);
    },
    getAllData() {
        let allDataList = [];
        for (const dataType of dataTypes) {
            const dataJson = localStorage.getItem(dataType);
            if (dataJson) {
                const typeDataList = JSON.parse(dataJson);
                typeDataList.forEach((item) => {
                    let dataTypeItem = {};
                    dataTypeItem.typeCode = dataType;
                    dataTypeItem.typeName = dataTypeMap[dataType];
                    dataTypeItem.option = item;
                    allDataList.push(dataTypeItem);
                });
            }
        }
        return allDataList;
    },
    deleteDataById(key, id, update) {
        if (!update && window.confirm('') === true) {
            let dataList = this.getDataList(key);
            dataList = dataList.filter(item => item.id !== id);
            localStorage.setItem(key, JSON.stringify(dataList));
            Toast.show({
                icon: 'success',
                content: '',
            });
        }
        if (update) {
            let dataList = this.getDataList(key);
            dataList = dataList.filter(item => item.id !== id);
            localStorage.setItem(key, JSON.stringify(dataList));
        }
    },
    updateDataById(key, dataItem) {
        this.deleteDataById(key, dataItem.id, true);
        dataItem.updateTime = new Date().getTime();
        this.pushDataList(key, dataItem, true);
    }
}
export default dataUtil;