export function autoTheme() {
    //media
    let media = window.matchMedia('(prefers-color-scheme:dark)');
    if (media.matches) {
        setDark();
        // setLight();
    } else {
        setLight();
        // setDark();
    }
    //
    media.addEventListener('change', (event) => {
        if (event.matches) {
            setDark();
        } else {
            setLight();
        }
    });
}

export function setDark() {
    document.documentElement.setAttribute('theme', 'dark');
    document.querySelector('meta[name="theme-color"]').setAttribute('content', 'rgba(0, 0, 0)');
    // document.querySelector('meta[name="theme-color"]').setAttribute('content', 'rgba(4, 6, 22, 1)');
}

export function setLight() {
    document.documentElement.setAttribute('theme', 'white');
    document.querySelector('meta[name="theme-color"]').setAttribute('content', 'rgba(242, 242, 247)');
    // document.querySelector('meta[name="theme-color"]').setAttribute('content', 'rgba(99, 171, 221, 0.8)');
}

export function setHeaderColor(color) {
    document.querySelector('meta[name="theme-color"]').setAttribute('content', color);
}