import SysMain from "../../component/SysMain";
import SysHead from "../../component/SysHead";
import DataSource from "../data/dataSource/DataSource";
import DataBase from "../data/database/DataBase";
import {useState} from "react";
import dataUtil from "../../static/js/dataUtil";
import {EditSFill} from "antd-mobile-icons";


function HomePage() {
    const [showDataBase, setShowDataBase] = useState(false);
    const [allData, setAllData] = useState([]);

    function initData() {
        const allData = dataUtil.getAllData();
        setAllData(allData);
    }
    
    return (
        <div>
            <SysHead
                right={
                    <>
                        <div onClick={() => {
                            setShowDataBase(true);
                            initData();
                        }}>
                            <EditSFill/>
                        </div>
                    </>
                }/>
            <SysMain type="topMain">
                <DataBase allData={allData} reloadData={() => {
                    initData()
                }} show={showDataBase} close={() => {
                    setShowDataBase(false);
                }}/>
                <DataSource/>
            </SysMain>
        </div>
    );
}

export default HomePage;