import HomePage from "./page/home/HomePage";
import React, {useState} from "react";
import GlobalStateContext from "./static/js/GlobalStateContext";

function App() {
    const [msgType, setMsgType] = useState('');
    const [msgText, setMsgText] = useState('');
    return (
        <GlobalStateContext.Provider value={{msgType, setMsgType, msgText, setMsgText}}>
            <HomePage/>
        </GlobalStateContext.Provider>
    );
}

export default App;
