import "../../../static/css/data/dataBase.css"
import {handleUndefined} from "../../../static/js/stringUtil";
import {CloseCircleOutline, DeleteOutline, EditFill} from "antd-mobile-icons";
import dataUtil from "../../../static/js/dataUtil";
import SysEmpty from "../../../component/SysEmpty";
import {useState} from "react";
import SysPopup from "../../../component/SysPopup";
import SysInput from "../../../component/SysInput";

function DataBase(props) {
    let show = handleUndefined(props.show, false);
    let close = handleUndefined(props.close, undefined);
    let reloadData = handleUndefined(props.reloadData, undefined);
    const allData = handleUndefined(props.allData, []);
    const [showEditForm, setShowEditForm] = useState(false);
    const [currentEditItem, setCurrentEditItem] = useState({option: ""})
    return (
        <div className={show ? "data-base open" : "data-base"}>
            <SysPopup title="" type='mid'
                      show={showEditForm} setShow={(value) => setShowEditForm(value)}
                      saveData={() => {
                          dataUtil.updateDataById(currentEditItem.typeCode, currentEditItem.option);
                          setShowEditForm(false);
                          reloadData();
                      }}>
                {
                    Object.keys(currentEditItem.option).map((key, index) => {
                        return (
                            <SysInput key={index} title={key} value={currentEditItem.option[key]}></SysInput>
                        );
                    })
                }
            </SysPopup>
            <div className="data-base-head">
                <div className="head-item-left">
                </div>
                <div className="head-item-mid">
                    
                </div>
                <div className="head-item-right">
                </div>
            </div>
            <div className="data-base-main">
                <SysEmpty show={allData.length === 0}></SysEmpty>
                {
                    allData.map((item, index) => {
                        return (
                            <div key={index} className="data-base-item">
                                <div className="data-base-item-content">
                                    <div className="title">{item.typeName}</div>
                                    <div className="info">
                                        {
                                            Object.keys(item.option).map((key, index) => {
                                                return (
                                                    <div key={index}
                                                         className="info-item">{key}{item.option[key]}</div>
                                                );
                                            })
                                        }
                                    </div>
                                </div>
                                <div className="data-base-item-operate">
                                    <div className="operate-item" onClick={() => {
                                        setCurrentEditItem(item);
                                        setShowEditForm(true);
                                    }}>
                                        <EditFill></EditFill>
                                    </div>
                                    <div className="operate-item" onClick={() => {
                                        dataUtil.deleteDataById(item.typeCode, item.option.id);
                                        reloadData();
                                    }}>
                                        <DeleteOutline></DeleteOutline>
                                    </div>
                                </div>
                            </div>
                        );
                    })
                }
            </div>
            <div className="data-base-foot">
                <div className="foot-item">
                    <CloseCircleOutline fontSize={35} onClick={close}></CloseCircleOutline>
                </div>
            </div>
        </div>
    );
}

export default DataBase;