import GlobalStateContext from "../static/js/GlobalStateContext";
import {CheckOutline} from 'antd-mobile-icons'

function SysSmartMsg() {

    return (
        <GlobalStateContext.Consumer>
            {({msgType, msgText}) => (
                <div className="sys-msg-container flex-center">
                    <div className={"flex-center sys-msg " + msgType}>
                        <div className="sys-msg-content flex-center"
                             style={{
                                 opacity: msgType !== '' ? 1 : 0,
                                 display: (msgType === 'toast') ? undefined : 'none'
                             }}>
                            <div className="msg-icon">
                                <CheckOutline/>
                            </div>
                            <div className="msg-text">
                                {msgText}
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </GlobalStateContext.Consumer>
    );
}

export default SysSmartMsg;